/**
 * Cloudflare Worker entry point for Laravel via php-cgi-wasm.
 *
 * On cold start: initializes PHP, fetches app.tar from Static Assets,
 * unpacks into Emscripten MEMFS, and caches the PHP instance.
 * Each request is routed through php.request(request).
 */

// Must be imported before any Emscripten module to shim document/window
import './shims';

import { PhpCgiCloudflare } from './php-custom';
import { untar } from './tar';

interface Env {
  ASSETS: Fetcher;
}

let php: PhpCgiCloudflare | null = null;
let initialized: Promise<void> | null = null;

async function ensureInitialized(env: Env): Promise<PhpCgiCloudflare> {
  if (!php) {
    php = new PhpCgiCloudflare({
      docroot: '/app/public',
      prefix: '/',
      entrypoint: 'index.php',
    });

    initialized = initializeFilesystem(php, env);
  }

  await initialized;
  return php;
}

async function initializeFilesystem(
  php: PhpCgiCloudflare,
  env: Env,
): Promise<void> {
  const FS = await php.getFS();

  // Fetch the compressed Laravel application from Static Assets
  const tarResponse = await env.ASSETS.fetch(
    new Request('http://assets.local/app.tar.gz'),
  );

  if (!tarResponse.ok) {
    throw new Error(`Failed to fetch app.tar.gz: ${tarResponse.status}`);
  }

  // Decompress gzip using Web Streams API (available in Workers)
  const decompressed = tarResponse.body!.pipeThrough(
    new DecompressionStream('gzip'),
  );
  const tarBuffer = await new Response(decompressed).arrayBuffer();

  // Unpack into MEMFS at /app
  untar(FS, tarBuffer, '/app');

  // Ensure required Laravel directories exist
  const dirs = [
    '/app/storage/framework/sessions',
    '/app/storage/framework/views',
    '/app/storage/framework/cache',
    '/app/storage/framework/cache/data',
    '/app/storage/logs',
    '/app/bootstrap/cache',
  ];

  for (const dir of dirs) {
    mkdirp(FS, dir);
    // Ensure writable — MEMFS mkdir applies umask (0022), resulting in 0755.
    // PHP needs write access to storage directories for views, cache, logs.
    FS.chmod(dir, 0o777);
  }

  // Debug: write a test PHP file to check FS permissions
  FS.writeFile(
    '/app/public/debug-perms.php',
    `<?php
header('Content-Type: text/plain');
echo "UID: " . posix_getuid() . "\\n";
echo "GID: " . posix_getgid() . "\\n";
echo "umask: " . decoct(umask()) . "\\n";
$dir = '/app/storage/framework/views';
echo "dir exists: " . (is_dir($dir) ? 'YES' : 'NO') . "\\n";
echo "dir writable: " . (is_writable($dir) ? 'YES' : 'NO') . "\\n";
$stat = stat($dir);
echo "dir mode: " . decoct($stat['mode']) . "\\n";
echo "dir uid: " . $stat['uid'] . "\\n";
echo "dir gid: " . $stat['gid'] . "\\n";
// Try to create a file
$testFile = $dir . '/test_write.txt';
$result = @file_put_contents($testFile, 'hello');
echo "file_put_contents: " . ($result !== false ? "OK ($result bytes)" : "FAILED - " . error_get_last()['message']) . "\\n";
// Try direct fopen
$fh = @fopen($testFile . '2', 'w');
echo "fopen: " . ($fh ? "OK" : "FAILED - " . error_get_last()['message']) . "\\n";
if ($fh) fclose($fh);
// Try touch
echo "touch: " . (@touch($testFile . '3') ? "OK" : "FAILED - " . error_get_last()['message']) . "\\n";
`,
  );

  // Provide a minimal iconv() stub so symfony/polyfill-mbstring works
  // without the native iconv extension. This is loaded before any PHP code.
  FS.writeFile(
    '/app/iconv-stub.php',
    `<?php
if (!function_exists('iconv')) {
    function iconv(string \$from, string \$to, string \$string): string|false {
        // For UTF-8 only environments, pass through
        \$from = strtoupper(\$from);
        \$to = strtoupper(\$to);
        // Strip //TRANSLIT and //IGNORE suffixes
        \$to = preg_replace('/\\/\\/(TRANSLIT|IGNORE)/', '', \$to);
        if ((\$from === 'UTF-8' || \$from === 'UTF8') && (\$to === 'UTF-8' || \$to === 'UTF8' || \$to === 'ASCII')) {
            if (\$to === 'ASCII') {
                // Strip non-ASCII bytes
                return preg_replace('/[\\x80-\\xFF]/', '', \$string);
            }
            return \$string;
        }
        // For same encoding, pass through
        if (\$from === \$to) {
            return \$string;
        }
        // Fallback: return as-is (best effort for UTF-8 environments)
        return \$string;
    }
}
if (!function_exists('iconv_strlen')) {
    function iconv_strlen(string \$string, ?string \$encoding = null): int|false {
        return strlen(\$string);
    }
}
if (!function_exists('iconv_strpos')) {
    function iconv_strpos(string \$haystack, string \$needle, int \$offset = 0, ?string \$encoding = null): int|false {
        return strpos(\$haystack, \$needle, \$offset);
    }
}
if (!function_exists('iconv_substr')) {
    function iconv_substr(string \$string, int \$offset, ?int \$length = null, ?string \$encoding = null): string|false {
        return \$length === null ? substr(\$string, \$offset) : substr(\$string, \$offset, \$length);
    }
}
// mb_split is part of mbregex (not in polyfill-mbstring).
// Laravel uses it only for whitespace splitting.
if (!function_exists('mb_split')) {
    function mb_split(string \$pattern, string \$string, int \$limit = -1): array|false {
        // mb_split uses Oniguruma regex; for basic patterns, PCRE /u works.
        return preg_split('/' . \$pattern . '/u', \$string, \$limit) ?: false;
    }
}
`,
  );

  // Write php.ini with auto_prepend_file to load stubs
  mkdirp(FS, '/config');
  FS.writeFile(
    '/config/php.ini',
    'auto_prepend_file=/app/iconv-stub.php\n',
  );
}

function mkdirp(
  FS: { analyzePath(p: string): { exists: boolean }; mkdir(p: string): void },
  path: string,
): void {
  const parts = path.split('/').filter(Boolean);
  let current = '';
  for (const part of parts) {
    current += '/' + part;
    if (!FS.analyzePath(current).exists) {
      FS.mkdir(current);
    }
  }
}

export default {
  async fetch(request: Request, env: Env): Promise<Response> {
    try {
      const instance = await ensureInitialized(env);

      const url = new URL(request.url);

      // Serve static assets from Vite build via Static Assets
      if (url.pathname.startsWith('/build/')) {
        const assetResponse = await env.ASSETS.fetch(request);
        if (assetResponse.ok) {
          return assetResponse;
        }
      }

      // Serve favicon and other public static files via Static Assets
      const staticExtensions = [
        '.ico',
        '.png',
        '.jpg',
        '.jpeg',
        '.gif',
        '.svg',
        '.css',
        '.js',
        '.woff',
        '.woff2',
        '.ttf',
      ];
      if (staticExtensions.some((ext) => url.pathname.endsWith(ext))) {
        const assetResponse = await env.ASSETS.fetch(request);
        if (assetResponse.ok) {
          return assetResponse;
        }
      }

      // Route everything else through PHP
      const response = await instance.request(request);

      return response;
    } catch (error) {
      const message =
        error instanceof Error ? error.message : 'Unknown error';
      const stack = error instanceof Error ? error.stack : '';
      console.error('Worker error:', message, stack);
      return new Response(`Internal Server Error: ${message}`, {
        status: 500,
        headers: { 'Content-Type': 'text/plain' },
      });
    }
  },
};
